.scorecard-container {
  padding: 10px;
  padding-top: 70px; /* Increased padding-top to clear menu */
  font-family: Arial, sans-serif;
  font-size: 0.9em; /* Overall larger font size for the container */
  margin-top: 0px; /* Removed margin-top */
}

.scorecard-container h2 {
  color: #333;
  margin-bottom: 10px;
  font-size: 1.3em; /* Slightly larger heading */
}

.dropdown-grid {
  display: grid;
  grid-template-columns: 1fr 1fr; /* Two equal columns */
  gap: 0px; /* Reduced space between columns and rows */
  margin-bottom: 10px; /* Reduced margin */
  justify-items: center; /* Center the items within the grid */
}

.controls-container {
  display: flex;
  flex-direction: column;
  align-items: center; /* Center the label and select */
  width: 100%; /* Ensure the div takes full width of its grid column */
}

.controls-container label {
  margin-bottom: 5px;
  font-weight: bold;
}

.controls-container select {
  padding: 8px;
  border-radius: 5px;
  border: 1px solid #ddd;
  font-size: 1.1em;
  width: 80%; /* Reduced width */
  max-width: 250px; /* Max width to prevent excessive widening */
  box-sizing: border-box;
}

.scorecard-grid-container {
  margin-top: 20px;
  width: 100%; /* Ensure it takes full width */
}

.scorecard-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 10px;
  /* Removed min-width */
}

.scorecard-table th,
.scorecard-table td {
  border: 1px solid #ddd;
  padding: 2px; /* Reduced padding */
  text-align: center;
  font-size: 0.8em; /* Reduced font size for table cells */
  white-space: normal; /* Allow text wrapping in headers/cells */
}

.scorecard-table thead {
  border: 2px solid #A9A9A9; /* Mid-grey border around the header */
}

.scorecard-table thead th {
  background-color: #A9A9A9; /* Mid-grey background */
  color: white; /* White text */
  font-weight: bold;
}

.scorecard-table th:nth-child(n+21) { /* Target calculated fields headers (adjust number if needed) */
  width: 60px; /* Smaller width for calculated fields */
}

.scorecard-table input[type="number"],
.stableford-value,
.summary-value {
  width: 30px; /* Reduced width */
  padding: 2px; /* Reduced padding */
  font-size: 0.9em; /* Reduced font size */
  border: 1px solid #ccc;
  border-radius: 2px;
  text-align: center;
  display: inline-block;
  box-sizing: border-box;
  height: 24px; /* Reduced height */
  line-height: 20px; /* Adjusted line-height */
}

/* Hide spin buttons for Chrome, Safari, Edge */
.scorecard-table input[type="number"]::-webkit-outer-spin-button,
.scorecard-table input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.details-grid {
  display: grid;
  grid-template-columns: repeat(3, minmax(120px, 1fr)); /* Further adjusted for narrower columns */
  gap: 5px; /* Further reduced gap */
  margin-top: 5px; /* Reduced margin */
}

.tournament-details,
.course-details,
.player-handicaps {
  border: 1px solid #ddd;
  padding: 3px; /* Further reduced padding */
  border-radius: 5px;
  background-color: #f9f9f9;
}

.player-handicaps table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 5px; /* Already reduced */
}

.player-handicaps th,
.player-handicaps td {
  border: 1px solid #eee;
  padding: 2px; /* Further reduced padding */
  text-align: left;
  font-size: 0.8em; /* Slightly smaller font size */
}

.player-handicaps th {
  background-color: #e0e0e0;
}

.player-handicaps td:nth-child(1) {
  width: 40%; /* Adjust width for player name column */
}

.player-handicaps td:nth-child(2) {
  width: 60%; /* Adjust width for handicap details column */
}

.initiate-scoring-button,
.submit-scores-button,
.end-round-button {
  margin-right: 10px; /* Add some space between buttons */
  background-color: #4CAF50; /* Green */
  border: none;
  color: white;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin-top: 20px;
  cursor: pointer;
  border-radius: 5px;
}

.initiate-scoring-button:hover {
  background-color: #45a049;
}

.eagle-score {
  background-color: #FFFFCC;
}

.birdie-score {
  background-color: #FFCCCC;
}

.bogey-score {
  background-color: #CCEEFF;
}

.double-bogey-score {
  background-color: #EEEEEE;
}

.worse-than-double-bogey-score {
  background-color: #E0CCFF;
}

.scorecard-color-key {
  margin-bottom: 5px; /* Reduced margin */
  padding: 5px; /* Reduced padding */
  border: 1px solid #ddd;
  border-radius: 5px;
  background-color: #f9f9f9;
  font-size: 0.8em;
}

.scorecard-color-key h4 {
  margin-top: 0;
  margin-bottom: 5px; /* Reduced margin */
  font-size: 1.0em; /* Slightly smaller heading */
  color: #333;
}

.scorecard-color-key ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  gap: 8px; /* Reduced gap */
  justify-content: center;
}

.scorecard-color-key li {
  display: flex;
  align-items: center;
}

.scorecard-color-key span {
  display: inline-block;
  width: 18px;
  height: 18px;
  border: 1px solid #ccc;
  margin-right: 8px;
  border-radius: 3px;
}

.eagle-score-key {
  background-color: #FFFFCC;
}

.birdie-score-key {
  background-color: #FFCCCC;
}

.bogey-score-key {
  background-color: #CCEEFF;
}

.double-bogey-score-key {
  background-color: #EEEEEE;
}

.worse-than-double-bogey-score-key {
  background-color: #E0CCFF;
}
